.light-bulb {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  font-size: 30px;
  line-height: 50px;
  margin: 10px;
  background-color: #ececec;
  -webkit-filter: drop-shadow(2px 2px 2px rgba(0, 0, 0, 0.7));
  filter: drop-shadow(2px 2px 2px rgba(0, 0, 0, 0.7));
  user-select: none;

  &.on {
    animation: fadeout 0.5s step-start;
  }
}

@keyframes fadeout {
  0% {
    background-color: #ececec;
  }
  50% {
    background-color: #ffde67;
  }
  100% {
    background-color: #ececec;
  }
}
